.homePage {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    .title {
        text-align: center;
        color: brown;
    }

    input[type="text"] {
        border-radius: 10px;
        padding: 10px;
        margin: 10px;
        align-self: center;
    }

    .card-container {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));

    }
}