.card {
    width: 100%;
    height: 500px;
    padding: 1em;

    .cardBody {
        border-radius: 10px;
        border: 2px salmon solid;
        width: 100%;
        height: 100%;
        text-overflow: ellipsis;
        padding: 10px;

        // TITLE AND ORIGIN 
        .titleCard {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            padding: 5px;

            h2 {
                text-align: center;
                font-weight: bold;
                width: 100%;
            }

            span {
                text-align: center;
                width: 100%;
                margin: 10px;
            }
        }

        // IMG
        .imgContainer {
            display: flex;
            justify-content: center;
            height: 200px;
        }

        // DESCRIPTION
        .description {
            margin-top: 10px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 6;
            overflow: hidden;
        }
    }
}